import React from 'react';
import { connect } from 'react-redux';
import { Formik, Form, Field } from 'formik';
import PropTypes from 'prop-types';
import { Divider, Input, Button } from 'antd';
import styled from 'styled-components';
import classNames from 'classnames';
import AuthWithTocken from '../../hoc/AuthWithTocken';
import EditableTagGroup from './AddTags';
import * as actionsArticles from '../../actions/articles';
import { formatAddArticleErrorsToFormikErrors } from '../../utilities/formatServerErrors';

const mapDispatchToProps = {
  addArticle: actionsArticles.addArticle,
};

const fieldErrorClassNames = (field, errors, touched) => {
  return classNames({ error: errors[field] && touched[field] });
};

const AddArticle = (props) => {
  const { addArticle } = props;

  return (
    <div className="content add-article-wrapper">
      <h1>Add new article</h1>
      <Divider />
      
      <Formik initialValues={{
        title: "",
        description: "",
        body: "",
        tagList: [],
      }} onSubmit={async (values, { setErrors }) => {
        try {
          await addArticle(values);
        } catch(err) {
          const newErrors = formatAddArticleErrorsToFormikErrors(err);
          setErrors(newErrors);
        }
      }}>
        {({ values, handleChange, errors, touched }) => (
          <Form>
            <InputWrapper>
              <Field as={Input} name="title" placeholder="Title" className={fieldErrorClassNames('title', errors, touched)} />
              {errors.title && touched.title && (
                <span className="error-text">{errors.title}</span>
              )}
            </InputWrapper>
            <InputWrapper>
              <Field as={Input} name="description" placeholder="Short description" className={fieldErrorClassNames('description', errors, touched)} />
              {errors.description && touched.description && (
                <span className="error-text">{errors.description}</span>
              )}
            </InputWrapper>
            <InputWrapper>
              <Field as={Input.TextArea} name="body" autoSize={{ minRows: 5 }} placeholder="Article body" className={fieldErrorClassNames('body', errors, touched)} />
              {errors.body && touched.body && (
                <span className="error-text">{errors.body}</span>
              )}
            </InputWrapper>

            <InputWrapper>
              <EditableTagGroup tagList={values.tagList} updateTagList={(tags) => {
                  console.log(tags, 'Пришедшие теги. Тут они при удалении должны удаляться')
                  handleChange({ target: { name: 'tagList', value: tags } })}
                } />
            </InputWrapper>

            <Button
              type="primary"
              htmlType="submit"
            >
              Add article
            </Button>
          </Form>
        )}
      </Formik>
    </div>
  );
};

const InputWrapper = styled.div`
  margin-bottom: 20px;

  & .error {
    border-color: tomato;
  }

  & .error-text {
    margin-left: 5px;
    color: tomato;
  }
`;

AddArticle.propTypes = {
  addArticle: PropTypes.func.isRequired,
};

const IfTockenExists = AuthWithTocken(AddArticle);

export default connect(null, mapDispatchToProps)(IfTockenExists);